import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import DogHistory from '../../components/dog/history';
import DogAddLog from '../../components/dog/addlog';

const MONTHS = ['jan', 'feb', 'mar', 'apr', 'mai', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'des'];

const initialState = {
	isLoading: false,
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
		this.setState({ isLoading: true });
        // await appState.getFingerprint();
        // await userStore.getInfo();
		await this.loadFeed();
		// await this.loadWorkoutSummary();
		// await this.loadDogs();
		this.setState({ isLoading: false });
    }

	async loadFeed() {
		const { userStore } = this.props.stores;
        await userStore.getFeed();
    }

	async loadWorkoutSummary() {
		const { userStore } = this.props.stores;
        await userStore.getWorkoutSummary();
    }

	loadDogs = async () => {
		this.setState({ isLoading: true });
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
		this.setState({ isLoading: false });
	}

	// componentWillUnmount() {
	//     PubSub.unsubscribe(subscription);
	// }
    componentDidMount() {
        this.loadAll();
    }

	render() {
		const { isLoading } = this.state;
		const { userStore, appState } = this.props.stores;
        const { darkmode } = appState;
		const {
			historyFeed,
		} = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 90px;'>
				<div class='row mb-4 fixed-top' style={`margin-top: 58px; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center'>
						<a href='/' class='text-secondary stretched-link'><i class='fas fa-chart-line' /> Dashboard</a>
					</div>
					<div class='col text-center border-bottom border-primary'>
						<a href='/feed' class='text-primary stretched-link'><i class='fas fa-rss' /> Feed</a>
					</div>
					<div class='col text-center'>
						<a href='/todo' class='text-secondary stretched-link'><i class='fas fa-list' /> Todo</a>
					</div>
				</div>
					
				<div class='row'>
					<div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<span class='float-right'><DogAddLog stores={this.props.stores} dog={{}} /></span>
							<h5>Siste fra hundene</h5>
						</div>

						{!isLoading && historyFeed && historyFeed.length === 0 && (
							<div class='mb-5 py-3 text-center border-top border-bottom text-muted'>
								<div class='display-4'>
									<i class='fas fa-dog' />
								</div>
								<h5>
									Ingen hendelser fra hundene.
								</h5>
								Det er fint å legge inn hendelser (løpetid, skader, vaksinering, vetrinærbesøk, mm) og bilder av hundene i loggen.<br />
								<br />
								På denne måten så vil hele teamet ditt se hva som skjer med hundene og hva som trengs å følges opp.
								Dette er en slags <strike>Face</strike>Dogbook for hundene deres.
								<br />
								<a class='btn btn-info mt-3' href='/dogs/'>
									<i class='fas fa-dog' /> Legg til logg på hundene
								</a>
							</div>
						)}

						{historyFeed && historyFeed.map(line => (
							<DogHistory stores={this.props.stores} line={line} />
						))}
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
